<template>
  <v-app>
    <!--  -->
    <v-app-bar app color="primary" dark>

      <!--  -->
      <v-toolbar-title
        @click="toRoot()"
        style="cursor: pointer"
      ><v-icon large v-if="!isRoot">mdi-chevron-left</v-icon>{{ appName }}</v-toolbar-title>

      <v-spacer></v-spacer>

      <!--  -->
      <v-toolbar-items v-if="isAuth">
        <!--  -->
        <v-menu
          offset-y
          open-on-hover
          :close-on-content-click="true"
        >
          <template v-slot:activator="{ on }">
            <!-- Google -->
            <v-btn text v-on="on" style="cursor: default"><v-avatar size="40" class="mr-3"><img :src="userIcon"></v-avatar>{{ userName }}</v-btn>
          </template>
          <v-list>
            <!--  -->
            <v-list-group
              v-if="isAdmin"
              v-model="isActiveAdminMenu"
              no-action
            >
              <template v-slot:activator>
                <v-list-item-content>
                  <v-list-item-title></v-list-item-title>
                </v-list-item-content>
              </template>
              <v-list-item
                v-for="adminMenuItem in adminMenuItems"
                link
                :key="adminMenuItem.title"
                :to="adminMenuItem.link"
              >
                <v-list-item-content>
                  <v-list-item-title>{{ adminMenuItem.title }}</v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list-group>

            <!--  -->
            <v-list-item link to="/settings">
              <v-list-item-content>
                <v-list-item-title></v-list-item-title>
              </v-list-item-content>
            </v-list-item>

            <!--  -->
            <v-list-item link>
              <v-list-item-content>
                <v-list-item-title @click="logout"></v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list>
        </v-menu>
      </v-toolbar-items>
    </v-app-bar>

    <!--  -->
    <v-main>
      <v-container fluid>
        <!-- router-view  -->
        <router-view></router-view>
      </v-container>
    </v-main>

    <!--  -->
    <v-snackbar v-model="snackbar" color="info">
      {{ message }}
      <template v-slot:action="{ attrs }">
        <v-btn text v-bind="attrs" @click="snackbar = false">Close</v-btn>
      </template>
    </v-snackbar>

    <!--  -->
    <v-dialog
      v-model="showSettingsAlert"
      transition="scale-transition"
      max-width="500px"
      persistent
    >
      <v-card>
        <v-toolbar color="primary" dark elevation-1>
          <v-btn icon @click="onClickCloseSettingsAlert = true"><v-icon>mdi-close</v-icon></v-btn>
          <v-toolbar-title></v-toolbar-title>
        </v-toolbar>
        <v-card-text class="pt-3">
          <router-link :to="{ path: '/settings' }" @click.native="onClickCloseSettingsAlert = true"></router-link>
        </v-card-text>
      </v-card>
    </v-dialog>

  </v-app>

</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'

export default {
  name: 'App',

  data () {
    return {
      appName: '',
      snackbar: false,
      message: '',
      isActiveAdminMenu: true,
      onClickCloseSettingsAlert: false,
      adminMenuItems: [
        {
          title: '',
          link: '/admins'
        },
        {
          title: '',
          link: '/departments'
        },
        {
          title: '',
          link: '/users'
        },
        {
          title: '',
          link: '/routes'
        },
      ],
    }
  },

  computed: {
    ...mapState({
      authMessage: state => state.firebase.authMessage,
      workflowMessage: state => state.firestore.workflowMessage,
      settingsMessage: state => state.workflow.settingsMessage,
      showSettingsMessage: state => state.workflow.showSettingsMessage,
      isAdmin: state => state.firebase.isAdmin,
    }),

    ...mapGetters({
      userName: 'firebase/getUserName',
      userIcon: 'firebase/getUserIcon',
      isAuth: 'firebase/getLoginStatus',
      getSettings: 'workflow/getSettings',
    }),

    isRoot () {
      const currentPath = this.$route.path
      if (currentPath == '/') {
        return true
      } else {
        return false
      }
    },

    /** 3 */
    showSettingsAlert () {
      /** 1computed(getters)isAuth */
      const isAuth = this.isAuth

      /** 2dataonClickCloseSettingsAlert(:false)
       *  true(template)
       */
      const onClickCloseSettingsAlert = this.onClickCloseSettingsAlert

      /** 3
       *  2(API URLAuth Token)()true
       */
      const hasEmptySetting = () => {
        const settings = this.getSettings
        const checkTargets = [
          settings.apiUrl == '' ? true : false,
          settings.authToken == '' ? true : false,
        ]
        return checkTargets.some(v => v == true)
      }

      /**  */
      const checkTargets = [
        isAuth,
        hasEmptySetting(),
        !onClickCloseSettingsAlert
      ]

      /** truetrue */
      return checkTargets.every(v => v == true)
    },
  },

  methods: {
    ...mapActions({
      toggleShowSettingsMessage: 'workflow/toggleShowSettingsMessage'
    }),

    async logout(){
      await this.$store.dispatch('firebase/logout')
    },

    toRoot() {
      const currentPath = this.$route.path

      if (currentPath != '/') {
        // 
        this.$router.push('/')
      } else {
        // 
        this.$router.go({ path: '/', force: true })
      }
    },

    setSiteTitle () {
      document.title = this.appName
    },
  },

  created () {
    this.setSiteTitle()
  },

  beforeCreate () {
    // state
    // beforeCreate()App  
    // https://qiita.com/ksh-fthr/items/2a9f173c706ef6939f93
    this.$store.dispatch('workflow/loadSettings')
  },


  watch: {
    authMessage () {
      this.message = this.authMessage
      this.snackbar = true
    },
    workflowMessage () {
      this.message = this.workflowMessage
      this.snackbar = true
    },
    showSettingsMessage () {
      const showSettingsMessage = this.showSettingsMessage
      if (showSettingsMessage) {
        this.message = this.settingsMessage
        this.snackbar = true
        this.toggleShowSettingsMessage()
      }
    },
  },
}
</script>
