<template>
  <!--  -->
  <v-dialog
    v-model="show"
    scrollable
    persistent
    max-width="500px"
    eager
  >
    <v-card>
      <v-card-title>{{ titleText }}</v-card-title>
      <v-divider/>
      <v-card-text>
        <v-form ref="form" v-model="valid">
          <!-- 
               ()
               v-model:userInfo
           -->
          <v-select
            label=""
            v-model="userInfo"
            :items="users"
            :item-text="users => `${users.name} (${users.id})`"
            return-object
          />
        </v-form>
      </v-card-text>
      <v-divider/>
      <v-card-actions>
        <v-spacer/>
        <v-btn
          color="grey darken-1"
          text
          @click="onClickClose"
        >
          
        </v-btn>
        <v-btn
          color="primary"
          text
          :disabled="!valid"
          @click="onClickAction"
        >
          {{ actionText }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
  name: 'ItemDialogAdmins',

  data () {
    return {
      /**  */
      currentTableName: 'admins',
      /**  */
      show: false,
      /**  */
      valid: false,
      /**  */
      menu: false,
      /**  'add' or 'edit' */
      actionType: 'add',

      userInfo: '',
      id: '',
      email: '',
      name: '',
    }
  },

  computed: {
    ...mapState({
      users: state => state.firestore.users,
    }),

    /**  */
    titleText () {
      return this.actionType === 'add' ? '' : ''
    },
    /**  */
    actionText () {
      return this.actionType === 'add' ? '' : ''
    }
  },

  methods: {
    ...mapActions(
      {
        fetchAllCollections: 'firestore/fetchAllCollections',
        addDocumentWithTextId: 'firestore/addDocumentWithTextId',
        updateDocument: 'firestore/updateDocument',
      }
    ),

    /**
     * 
     * 
     */
    open (actionType, item) {
      this.show = true
      this.actionType = actionType
      this.getUsers()
      this.resetForm(item)
    },

    /**  */
    onClickClose () {
      this.show = false
    },

    /**  */
    async onClickAction () {
      const item = {
        id: this.userInfo.id,
        name: this.userInfo.name,
      }
      const currentTableName = this.currentTableName

      if (this.actionType === 'add') {
        await this.addDocumentWithTextId({ item, currentTableName })
      } else {
        await this.updateDocument({ item, currentTableName })
      }

      this.show = false
    },

    /**  */
    resetForm (item = {}) {
      this.id = item.id || ''
      this.userInfo = { name: item.name, email: item.email } || ''

      this.$refs.form.resetValidation()
    },

    /**   */
    async getUsers() {
      const currentTableName = 'users'
      await this.fetchAllCollections({ currentTableName })
    },

  }
}
</script>
