<template>
  <!--  -->
  <v-dialog
    v-model="show"
    scrollable
    persistent
    max-width="500px"
    eager
  >
    <v-card>
      <v-card-title>{{ titleText }}</v-card-title>
      <v-divider/>
      <v-card-text>
        <v-form ref="form" v-model="valid">
          <!--  -->
          <v-text-field
            label=""
            v-model="department"
            :rules="departmentRules"
          />

        </v-form>
      </v-card-text>
      <v-divider/>
      <v-card-actions>
        <v-spacer/>
        <v-btn
          color="grey darken-1"
          text
          @click="onClickClose"
        >
          
        </v-btn>
        <v-btn
          color="primary"
          text
          :disabled="!valid"
          @click="onClickAction"
        >
          {{ actionText }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  name: 'ItemDialogDepartments',

  data () {
    return {
      /**  */
      currentTableName: 'departments',
      /**  */
      show: false,
      /**  */
      valid: false,
      /**  */
      menu: false,
      /**  'add' or 'edit' */
      actionType: 'add',
      /** ID */
      id: '',
      /**  */
      department: '',

      /**  */
      departmentRules: [
        v => v.trim().length > 0 || '',
        v => v.length <= 32 || '32'
      ],
    }
  },

  computed: {
    /**  */
    titleText () {
      return this.actionType === 'add' ? '' : ''
    },
    /**  */
    actionText () {
      return this.actionType === 'add' ? '' : ''
    }
  },

  methods: {
    ...mapActions(
      {
        addDocument: 'firestore/addDocument',
        updateDocument: 'firestore/updateDocument'
      }
    ),

    /**
     * 
     * 
     */
    open (actionType, item) {
      this.show = true
      this.actionType = actionType
      this.resetForm(item)
    },

    /**  */
    onClickClose () {
      this.show = false
    },

    /**  */
    async onClickAction () {
      const item = {
        id: this.id,
        department: this.department,
      }
      const currentTableName = this.currentTableName

      if (this.actionType === 'add') {
        await this.addDocument({ item, currentTableName })
      } else {
        await this.updateDocument({ item, currentTableName })
      }

      this.show = false
    },

    /**  */
    resetForm (item = {}) {
      this.id = item.id || ''
      this.department = item.department || ''

      this.$refs.form.resetValidation()
    }
  }
}
</script>
