<template>
  <v-row justify="center">
    <v-col cols="12" md="6" xs="12">
      <v-card>
        <v-card-text>
          <p><br></p>
          <v-form v-model="valid">
            <h3></h3>
            <!-- API URL -->
            <v-text-field
              label="API URL"
              v-model="settings.apiUrl"
              :counter="150"
              :rules="apiUrlRules"
            />
            <!-- Auth Token -->
            <v-text-field
              label="Auth Token"
              v-model="settings.authToken"
              :counter="36"
              :rules="authTokenRules"
            />
            <v-row class="mt-4 px-3 pb-3">
              <v-spacer/>
              <v-btn color="primary" :disabled="(!valid || saveButtonDisabled)" @click="onClickSave"></v-btn>
            </v-row>
          </v-form>
        </v-card-text>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
// import { mapState } from 'vuex'
export default {
  name: 'Settings',

  data () {
    return {
      settings: { ...this.$store.state.workflow.settings},
      valid: false,
      saveButtonDisabled: false,

      /**  */
      apiUrlRules: [
        v => v.trim().length > 0 || 'API URL',
        v => v.trim().length <= 150 || '150',
      ],
      authTokenRules: [
        v => v.trim().length > 0 || 'Auth Token',
        v => v.trim().length == 36 || '36',
      ],
    }
  },

  methods: {
    onClickSave () {
      this.$store.dispatch('workflow/saveSettings', { settings: this.settings })
      this.saveButtonDisabled = !this.saveButtonDisabled
    }
  },
}
</script>
