<template>
  <!--  -->
  <v-dialog
    v-model="show"
    scrollable
    persistent
    max-width="500px"
    eager
  >
    <v-card>
      <v-card-title>{{ titleText }}</v-card-title>
      <v-divider/>
      <v-card-text>
        <v-form ref="form" v-model="valid">
          <!-- 
            * :items => state()
            * item-text => 
            * item-text => 
          -->
          <v-select
            label=""
            v-model="department"
            :rules="departmentRules"
            :items="departments"
            item-text="department"
            item-value="department"
          />

          <!--  -->
          <v-text-field
            label=""
            v-model="email"
            :rules="emailRules"
            :readonly="actionType=='edit'"
          />

          <!--  -->
          <v-text-field
            label=""
            v-model="name"
            :rules="nameRules"
          />

        </v-form>
      </v-card-text>
      <v-divider/>
      <v-card-actions>
        <v-spacer/>
        <v-btn
          color="grey darken-1"
          text
          @click="onClickClose"
        >
          
        </v-btn>
        <v-btn
          color="primary"
          text
          :disabled="!valid"
          @click="onClickAction"
        >
          {{ actionText }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
  name: 'ItemDialogUsers',

  data () {
    return {
      /**  */
      currentTableName: 'users',
      /**  */
      show: false,
      /**  */
      valid: false,
      /**  */
      menu: false,
      /**  'add' or 'edit' */
      actionType: 'add',
      /** ID */
      id: '',
      /**  */
      department: '',
      /**  */
      email: '',
      /**  */
      name: '',

      /**  */
      departmentRules: [
        v => v.trim().length > 0 || '',
      ],
      emailRules: [
        v => v.trim().length > 0 || '',
        v => v.trim().length <= 32 || '32',
      ],
      nameRules: [
        v => v.trim().length > 0 || '',
        v => v.trim().length <= 32 || '32',
      ],
    }
  },

  computed: {
    ...mapState({
      departments: state => state.firestore.departments,
    }),

    /**  */
    titleText () {
      return this.actionType === 'add' ? '' : ''
    },
    /**  */
    actionText () {
      return this.actionType === 'add' ? '' : ''
    }
  },

  methods: {
    ...mapActions(
      {
        addDocumentWithTextId: 'firestore/addDocumentWithTextId',
        updateDocument: 'firestore/updateDocument',
        fetchAllCollections: 'firestore/fetchAllCollections',
      }
    ),

    /**
     * 
     * 
     */
    open (actionType, item) {
      this.show = true
      this.actionType = actionType
      this.getDepartments()
      this.resetForm(item)
    },

    /**  */
    onClickClose () {
      this.show = false
    },

    /**  */
    async onClickAction () {
      const item = {
        id: this.email,
        department: this.department,
        name: this.name,
      }
      const currentTableName = this.currentTableName

      if (this.actionType === 'add') {
        await this.addDocumentWithTextId({ item, currentTableName })
      } else {
        await this.updateDocument({ item, currentTableName })
      }

      this.show = false
    },

    /**  */
    resetForm (item = {}) {
      this.id = item.id || ''
      this.department = item.department || ''
      this.email = item.id || ''
      this.name = item.name || ''

      this.$refs.form.resetValidation()
    },

    /**  */
    async getDepartments() {
      const currentTableName = 'departments'
      await this.fetchAllCollections({ currentTableName })
    }
  },

}
</script>
