<template>
  <!--  -->
  <v-dialog
    v-model="show"
    scrollable
    persistent
    max-width="700px"
    eager
  >
    <v-card>
      <v-card-title>{{ titleText }}</v-card-title>
      <v-divider/>
      <v-card-text>
        <v-form ref="form" v-model="valid">
          <!-- 
            * :items => state()
            * item-text => 
            * item-value => 
          -->
          <v-select
            label=""
            v-model="department"
            :rules="departmentRules"
            :items="departments"
            item-text="department"
            item-value="department"
          />

          <!--  -->
          <v-text-field
            label=""
            v-model="title"
            :rules="titleRules"
          />

          <!--  -->
          <v-text-field
            label=""
            v-model="approvers.length"
            :rules="numberOfApproversRules"
            readonly
          />

          <v-data-table
            class="text-no-wrap"
            :headers="tableHeaders"
            :items="approvers"
            :footer-props="footerProps"
            :sort-by="['order']"
            :sort-desc="[false]"
            :items-per-page="30"
            mobile-breakpoint="0"
            hide-default-footer
          >

            <!--  -->
            <template v-slot:[`item.actions`]="{ item }">
              <v-icon @click="deleteApprover(item)">mdi-delete</v-icon>
            </template>
          </v-data-table>

          <!--  -->
          <!-- 
               ()
               v-model:userInfo
           -->
          <v-select
            label=""
            v-model="userInfo"
            :items="users"
            :item-text="users => `${users.name} (${users.id})`"
            return-object
          />
          <v-spacer/>
          <!--  -->
          <v-btn
            color="primary"
            text
            :disabled="!userInfo.id"
            @click="addApprover"
          ></v-btn>
        </v-form>
      </v-card-text>
      <v-divider/>
      <v-card-actions>
        <v-spacer/>
        <v-btn
          color="grey darken-1"
          text
          @click="onClickClose"
        >
          
        </v-btn>
        <v-btn
          color="primary"
          text
          :disabled="!valid"
          @click="onClickAction"
        >
          {{ actionText }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
  name: 'ItemDialogRoutes',

  data () {
    return {
      /**  */
      currentTableName: 'routes',
      /**  */
      currentTabName: '',
      /**  */
      show: false,
      /**  */
      valid: false,
      /**  */
      menu: false,
      /**  'add' or 'edit' */
      actionType: 'add',
      /** ID */
      id: '',
      /**  */
      department: '',
      /**  */
      title: '',
      /**  */
      userInfo: '',
      /**  */
      numberOfApprovers: '',

      /** test */
      approvers: [],
      readers: [],

      /**  */
      departmentRules: [
        v => v.trim().length > 0 || '',
      ],
      titleRules: [
        v => v.trim().length > 0 || '',
      ],
      numberOfApproversRules: [
        v => v > 0 || '',
      ],
    }
  },

  computed: {
    ...mapState({
      departments: state => state.firestore.departments,
      users: state => state.firestore.users,
    }),

    /**  */
    titleText () {
      return this.actionType === 'add' ? '' : ''
    },

    /**  */
    actionText () {
      return this.actionType === 'add' ? '' : ''
    },

    /**  */
    tableHeaders () {
      return [
        { text: '', value: 'order', sortable: false },
        { text: '', value: 'email', sortable: false },
        { text: '', value: 'name', sortable: false },
        { text: '', value: 'actions', sortable: false },
      ]
    },

    /**  */
    footerProps () {
      return { itemsPerPageText: '', itemsPerPageOptions: [30, -1] }
    },
  },

  methods: {
    ...mapActions(
      {
        addDocument: 'firestore/addDocument',
        updateDocument: 'firestore/updateDocument',
        fetchAllCollections: 'firestore/fetchAllCollections',
      }
    ),

    /**
     * 
     * 
     */
    open (actionType, item, currentTabName) {
      this.show = true
      this.actionType = actionType
      this.currentTabName = currentTabName
      this.getDepartments()
      this.getUsers()
      this.resetForm(item)
    },

    /**  */
    onClickClose () {
      this.show = false
    },

    /**  */
    async onClickAction () {
      const item = {
        id: this.id,
        request_type: this.currentTabName,
        department: this.department,
        title: this.title,
        approvers: this.approvers,
        // readers: this.readers
      }

      const currentTableName = this.currentTableName

      if (this.actionType === 'add') {
        await this.addDocument({ item, currentTableName })
      } else {
        await this.updateDocument({ item, currentTableName })
      }

      this.show = false
    },

    /**  */
    addApprover () {
      const approvers = {
        order: this.approvers.length + 1,
        email: this.userInfo.id,
        name: this.userInfo.name,
      }

      /**  */
      this.approvers.push(approvers)

      /**  */
      this.userInfo = []
    },

    /**  */
    deleteApprover(item) {
      const list = this.approvers
      const index = list.indexOf(item)
      list.splice(index, 1)
    },

    /**  */
    resetForm (item = {}) {
      this.id = item.id || ''
      this.department = item.department || ''
      this.title = item.title || ''
      this.approvers = item.approvers || []
      this.readers = item.readers || []

      this.$refs.form.resetValidation()
    },

    /**   */
    async getDepartments() {
      const currentTableName = 'departments'
      await this.fetchAllCollections({ currentTableName })
    },

    /**   */
    async getUsers() {
      const currentTableName = 'users'
      await this.fetchAllCollections({ currentTableName })
    },

  }
}
</script>
