<template>
  <!--  -->
  <v-dialog
    v-model="show"
    persistent
    max-width="290px"
  >
    <v-card>
      <v-card-title/>
      <v-card-text class="black--text">
        
      </v-card-text>
      <v-card-actions>
        <v-spacer/>
        <v-btn color="grey" text @click="onClickClose"></v-btn>
        <v-btn color="error" text @click="onClickDelete"></v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  name: 'DeleteDialog',

  data () {
    return {
      /**  */
      show: false,
      /**  */
      item: {},
      currentTableName: ''
    }
  },

  methods: {
    ...mapActions(
      {deleteDocument: 'firestore/deleteDocument'}
    ),

    /**
     * 
     * 
     */
    open (item, currentTableName) {
      this.show = true
      this.item = item
      this.currentTableName = currentTableName
    },
    /**  */
    onClickClose () {
      this.show = false
    },
    /**  */
    async onClickDelete () {
      const item = this.item
      const currentTableName = this.currentTableName

      await this.deleteDocument({ item, currentTableName })
      this.show = false
    }
  }
}
</script>
